
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import {mapState} from 'vuex';
export default {
name:'topicllist',
computed:{
...mapState(['topics'])
},
components:{
Cell,Group,Scroller,Tab,TabItem
},
props:['name'],
methods:{
getDescByTab(tab){
return this.$store.getters.getTopicDescByTab(tab);
},
routeto(index){

console.log(index);
this.$router.push('/topicllist/'+this.topics[index].name);
},
loadMoreData(){
var app=this;
this.page=this.page+1;
this.$http.get('/topics',{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(res){
for(var i=0;i<res.data.data.length;i++){
app.lists.push(res.data.data[i]);
}

app.$refs.pullup.donePullup();
})

},
freshData(){

var app=this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:1,
limit:this.limit
}
}).then(function(res){
app.lists=res.data.data;

app.$refs.pullup.donePulldown();
})
}
},
data:function(){
return{
lists:[],
page:1,//当前页数默认第一页
limit:10,
pullupConfig:{//上拉加载配置信息
content:'上拉加载更多',
downContent:'松开进行加载',
upContent:'上拉加载更多',
loadingContent:'加载中....'
},
pulldownConfig:{
content:'下拉刷新',
downContent:'松开刷新数据',
upContent:'下拉刷新数据',
loadingContent:'加载中....'
}
}
},
beforeRouteEnter:function(to,from,next){




next(function(vm){

vm.$http.get('/topics',{
params:{
tab:to.params.name,
page:vm.page,
limit:vm.limit
}
}).then(function(res){
console.log(res);
vm.lists=res.data.data
})
})

},
beforeRouteUpdate:function(to,from,next){
var app=this;
this.$http.get('/topics',{
params:{
tab:to.params.name,
page:this.page,
limit:this.limit
}
}).then(function(res){
if(res.data.success){
app.lists=res.data.data;
}
})
next();
}

}



