
import VmBackTop from 'vue-multiple-back-top';
import {mapState,mapGetters,mapMutations} from 'vuex';
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

export default {
name: 'app',
data:function(){
return{

}
},
components:{
XHeader,
Actionsheet,
VmBackTop
},
methods:{
...mapMutations(['mutationIsLogin','mutationAccesstoken']),
routetohome(){
this.$router.replace('/');
},
handleClickMoreMenu(menuKey,menuItem){
this.$router.push('/'+menuKey);
},

showMoreMenus(){ //右侧点击后显示
this.isShowMore=true;
}
},
mounted:function(){
var loginStatus=window.localStorage.getItem('loginStatus');
if(loginStatus){//目前是登录状态
this.mutationIsLogin(
window.localStorage.getItem('loginname')
);
this.mutationAccesstoken(
window.localStorage.getItem('accesstoken')
)
}
},
computed:{
...mapGetters(['moreMenus']),
...mapState(['isLogined','unLoginedMoreMenus','LoginedMoreMenus']),
isShowMore:{
get:function(){return this.$store.state.isShowMore},
set:function(nval){this.$store.commit('mutationIsShowMore',nval)}
},


}
}



